import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';
import { loadToys } from '../store/actions/toy.action';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect, useRef } from 'react'
ChartJS.register(ArcElement, Tooltip, Legend);

export function MyChart() {
    const { toys } = useSelector(state => state.toyModule)
    const dispatch = useDispatch()
    const dateRef = useRef()
    const labelsPriceMap = {}


    useEffect(() => {
        dispatch(loadToys())
    }, [])

    const data = {
        labels: ['Doll', 'Battery Powered', 'Funny', 'Smart', 'Dirty'],
        datasets: [
            {
                label: '# of Votes',
                data: [],
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                ],
                borderWidth: 2,
            },
        ],
    };
    if (!toys) return <div>Loading...</div>
    data.labels.forEach(label => {
        let toyCount = 0
        const toySumByType = toys.reduce((acc, toy) => {
            if (toy.labels.includes(label)) {
                acc += +toy.price
                toyCount++
            }
            return acc
        }, 0)
        labelsPriceMap[label] = toySumByType / toyCount
    })


    for (const i in labelsPriceMap) {
        data.datasets[0].data.push(labelsPriceMap[i])
    }
    return <div className="doughnut-container"><Doughnut data={data} /></div>
}
