import { useEffect } from 'react'
import { useState } from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import { toyService } from '../services/toy.service'

export const ToyDetails = () => {

    const [toy, setToy] = useState(null)
    const params = useParams()
    const navigate = useNavigate()

    useEffect(() => {
        loadToy()
    }, [params.toyId])

    const loadToy = () => {
        const toyId = params.toyId
        console.log(params.toyId)
        toyService.getById(toyId).then(toy => {
            setToy(toy)
        })
    }

    const onBack = () => {
        navigate('/toy')
    }



    if (!toy) return <div>Loading...</div>
    return (
        <div className='toy-details'>
            <h3>Name: {toy.name}</h3>
            <h3>Price: {toy.price}</h3>
            <h3>Labels: {toy.labels.join(', ')}</h3>
            <h3>Created At: {new Date(toy.createdAt).toLocaleDateString()}</h3>
            <h3>Available? {toy.inStock? "Yes": "No"}</h3>
            <h3>Reviews: {toy.reviews.join(', ')}</h3>
            <img src={`https://robohash.org/${toy._id}?set=set4`} alt="" />
            <button onClick={onBack}>Back to Toys App</button>
            <Link to={`/toy/edit/${toy._id}`}><button>Edit</button></Link>

        </div>
    )
}
