import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { ToyFilter } from '../cmp/toy-filter'
import { ToyList } from '../cmp/toy-list'
import { loadToys, removeToy, setFilterBy } from '../store/actions/toy.action'

export const ToyApp = (props) => {

    const toys  = useSelector(state => state.toyModule.toys)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(loadToys())
    }, [])

    const onRemoveToy = (toyId, ev) => {
        // ev.stopPropagation()
        dispatch(removeToy(toyId))
    }

    const onChangeFilter = (filterBy) => {
        dispatch(setFilterBy(filterBy))
        dispatch(loadToys())
    }

    if (!toys) return <div>Loading...</div>
    return (
        <div className='toy-app'>
            Hello from Toy App!
            <ToyFilter onChangeFilter={onChangeFilter} />
            <Link to={`/toy/edit`}><button>Add Toy</button></Link>
            <ToyList toys={toys} onRemoveToy={onRemoveToy} />
        </div>
    )
}
